import { address } from '@/store/environment';
import { defineComponent } from 'vue';
import { useGlobalStore } from '../store/global';
import { LoginModel } from '../models/loginModel';
import { storeToRefs } from 'pinia';

export default defineComponent({


    setup() {
        const { token } = storeToRefs(useGlobalStore())
        let loginModel: any = { LoginName: '', Password: '', RememberMe: false }

        return {
            loginModel,
            token
        }
    },
    data() {
        return {
            loading: false,
            showPassword: false,

        }
    },

    methods: {
        togglePassword() {
            this.showPassword = !this.showPassword;
        }
        ,
        async login() {
            const form = new FormData();
            Object.keys(this.loginModel).forEach((key) => form.append(key, this.loginModel[key]));

            return fetch(address() + "/token/get", {
                body: form,
                method: "POST",
            }).then(response => {
                console.log(response);

                if (response.status == 200) {

                    response.text().then(tk => {
                        this.token = tk
                        this.$router.push("/")
                    })
                }



            }).catch(error => {
            })

        }

    },

})
